import { prefix } from '../../globals/settings';
import { html, LitElement, property } from 'lit-element';
import { carbonElement as customElement } from '../../globals/decorators/carbon-element';
import styles from './avatar.scss';

/**
 * Section Title.
 *
 * @element cds-avatar
 */

@customElement(`${prefix}-avatar`)
export class CDSAvatar extends LitElement {
  @property({ type: String }) symbols = 'VK';
  @property({ type: String }) backgroundColor = '';
  @property({ type: String }) avatarText = '';

  static styles = styles;
  render() {
    return html` <div class="container">
      <div class="avatar">${this.symbols}</div>
      ${this.avatarText
        ? html`<p class="avatar-text">${this.avatarText}</p>`
        : ``}
    </div>`;
  }
}
